export class Accion {
  nombre: string;
  icono?: Icono;
  urlServicio?: string;
  rutaRedireccionar?: string;

  constructor(nombre: string,
              icono?: Icono,
              urlServicio?: string,
              rutaRedireccionar?: string) {
    this.nombre = nombre;
    this.icono = icono;
    this.urlServicio = urlServicio;
    this.rutaRedireccionar = rutaRedireccionar;
  }
}

export class Icono {
  nombre: string;
  clase: string; //nombre en material.icons
  constructor (nombre: string,
               clase: string) {
    this.nombre = nombre;
    this.clase = clase;
  }
}

export const AccionesPrueba: Accion[] = [
  new Accion("Editar",new Icono("edit","btn-success"),"",""),
  new Accion("Eliminar",new Icono("delete","btn-danger"),"",""),
  new Accion("Descargar",new Icono("file_download","btn-info"),"",""),
];



